import {mapGetters, mapState} from 'vuex';
import appHead from "../components/app-head.vue";

export default {
    data() {
        return {
            detail: {
                profit_price: '',
                total_price: '',
                total_goods_price: '',
                total_goods_original_price: '',
                created_at: '',
                order_no: '',
                pay_time: '0000-00-00 00:00:00',
                send_time: '0000-00-00 00:00:00',
                confirm_time: '0000-00-00 00:00:00',
                sale_time: '0000-00-00 00:00:00',
            },
            d: '',
            h: '',
            m: '',
            s: '',
            windowHeight: 0,
            timeInterval: null,
            showQr: false,
            is_user: false,
            show: false,
            iphone_x: false
        }
    },
    components: {
        appHead
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin,
        })
    },
    methods: {
        // 取消 | 申请退款
        cancel(e) {
            let self = this;
            uni.navigateTo({
                url: '/pages/order/refund/order-refund?id=' + self.detail.id
            });
        },
        toBig() {
            if(this.detail.status_text == '已提货' || this.detail.status_text == '已完成') {
                return false
            }
            this.showQr = !this.showQr
        },
        toRemind() {
            this.show = !this.show;
        },
        beConfirm() {
            let that = this;
            that.show = false;
            uni.showLoading({
                mask: true,
                title: '确认提货中...'
            });
            that.$request({
                url: that.is_user ? that.$api.order.confirm : that.$api.community.confirm,
                data: {
                    id: that.detail.id
                }
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.getDetail();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            })
        },
        // 订单支付
        orderPay(e) {
            let _this = this;
            _this.$showLoading();
            _this.$request({
                url: _this.$api.order.list_pay_data,
                data: {
                    id: this.detail.id
                }
            }).then(response => {
                _this.$hideLoading();
                if (response.code === 0) {
                    _this.$payment.pay(response.data.id).then(() => {
                        // 支付成功
                        uni.redirectTo({
                            url: '/plugins/community/order/order?is_user=1'
                        })
                    }).catch(e => {
                        // 支付失败
                        uni.showModal({
                            title: '',
                            content: e.errMsg,
                            showCancel: false
                        })
                    })
                }
            }).catch(() => {
                _this.$hideLoading();
            });
        },
        copy() {
            this.$utils.uniCopy({
                data: this.detail.order_no,
                success() {



                },
            });
        },
        tel() {
            let phoneNumber = this.detail.mobile
            if(this.is_user) {
                phoneNumber = this.detail.middleman_info.mobile
            }
            uni.makePhoneCall({
                phoneNumber: phoneNumber
            })
        },
        getDetail() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.is_user ? that.$api.community.order_detail : that.$api.community.middle_detail,
                data: {
                    id: this.id
                },
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                that.$hideLoading();
                if(response.code === 0) {
                    that.detail = response.data.detail;
                    that.getTime(that.detail.cancel_time_stamp);
                    let time = +that.detail.cancel_time_stamp - 1;
                    if(time > 0) {
                        that.timeInterval = setInterval(() =>{
                            time--;
                            if(time < 0) {
                                clearInterval(that.timeInterval);
                                that.getDetail();
                            }else {
                                that.getTime(time);
                            }
                        },1000);
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                    if(response.msg == '该订单您无权确认') {
                        setTimeout(()=>{
                            uni.redirectTo({
                                url: '/pages/index/index'
                            });
                        },1000)
                    }
                }
            }).catch(response => {
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        getTime(time) {
            let that = this;
            if(time > 86399) {
                this.d = Math.floor(time / 86400)
                time = time - (86400*this.d)
            }else {
                this.d = 0
            }
            if(time > 3599) {
                this.h = Math.floor(time / 3600)
                if(this.h < 10) {
                    this.h = '0' + this.h
                }
                time = time - (3600*this.h)
            }else {
                this.h = '00'
            }
            if(time > 59) {
                this.m = Math.floor(time / 60)
                if(this.m < 10) {
                    this.m = '0' + this.m
                }
                time = time - (60*this.m)
            }else {
                this.m = '00';
            }
            if(time < 60) {
                this.s = time;
                if(this.s < 10) {
                    this.s = '0' + this.s
                }
            }
        },
    },
    onHide() {
        clearInterval(this.timeInterval);
    },
    onUnload() {
        clearInterval(this.timeInterval);
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if(options.is_user == 1) {
            that.is_user = true
        }
        uni.getSystemInfo({
            success: function (res) {
                that.windowHeight = res.windowHeight;
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        that.id = options.id;
        that.getDetail();
    }
}
